.header {
  padding-top: 20px;
  padding-bottom: 20px;
  margin-bottom: auto;
  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__button {
    flex: 0 1 auto;
    position: relative;
    padding-right: 25px;
    margin-right: 15px;
    z-index: 100;
  }
  &__burger {
    position: absolute;
    width: 18px;
    height: 12px;
    top: 50%;
    margin-top: -6px;
    right: 0;
    span {
      display: block;
      height: 1px;
      position: absolute;
      width: 100%;
      top: 5px;
      left: 0;
      background-color: $color-text;
      transition: transform $transition, opacity $transition, background-color $transition;
    }
    span:first-child {
      transform-origin: 0% 50%;
      top: 0;
    }
    span:last-child {
      top: auto;
      bottom: 0;
      transform-origin: 0% 50%;
      transform: scaleX(0.6);
    }    
  }

  &__button.active &__burger {
    span {
      transform: scaleX(0);
      opacity: 0;
      background-color: $color-accent;
    }
    span:first-child {
      transform: rotate(45deg) translate(1px, -2px);
      opacity: 1;
    }
    span:last-child {
      transform: rotate(-45deg) scaleX(1) translate(1px, 2px);
      opacity: 1;
    }
  }

  &__call {
    flex: 0 0 auto;
  }

  &__menu {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: $color-background;
    z-index: 10;
    opacity: 0;
    transform: translate(0%, 100%) scale(1);
    transition: opacity $transition, transform $transition;
    display: flex;
    align-items: center;
    justify-content: center;

    &__wrapper {
      
    }

    &__list {
      list-style: none;
      padding-left: 10px;
      margin: 0;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      ul {
        list-style: none;
        padding-left: 0;
        margin: 0;
      }
      & > li > a, & > li > span {
        @include all-h2; 
        cursor: pointer;
        transition: color $transition;
        position: relative;
        z-index: 4;
        padding-left: 15px;
        &::before {
          content: '';
          width: 10px;
          height: 11px;
          background: url('../img/menu-mark.svg') no-repeat center center;
          background-size: contain;
          position: absolute;
          left: 0;
          top: 50%;
          transform: translate(-100%, -50%);
          opacity: 0;
          transition: opacity $transition, transform $transition;
        }
      }
      & > li {
        display: inline-flex;
        position: relative;
        align-items: center;
        padding-right: 15px;
      }
    }
    &__sublist {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      position: absolute;
      z-index: 3;
      top: 50%;
      left: 100%;
      opacity: 0;
      transform: translate(0, -50%);
      transition: opacity $transition;
      margin: 0 (-5em/16) 0 calc(#{-5em/16} - 1px);
      width: 100vw;
      min-height: 100%;
      pointer-events: none;
      a {
        padding: (5em/16);
        margin: 0 (5em/16);
        color: $color-text-gray;
        transition: color $transition;
        white-space: nowrap;
        &:hover {
          color: $color-text;
        }
      }
    }
  }
}

.header.active-menu {
  .header__menu {
    opacity: 1;
    transform: translate(0, 0) scale(1);
  }
}

.touch-browser .header__menu__list {
  & > li {
    align-items: flex-start;
    &.active {
      & > a, & > span {
        color: $color-accent;
        &::before {
          opacity: 1;
          transform: translate(0%,-50%);
        }
      }
      .header__menu__sublist {
        opacity: 1;
        pointer-events: all;
      }
    }
  }
} 
.courser-browser .header__menu__list {
  & > li {
    &:hover {
      & > a, & > span {
        color: $color-accent;
        &::before {
          opacity: 1;
          transform: translate(0%,-50%);
        }
      }
      .header__menu__sublist {
        opacity: 1;
        pointer-events: all;
      }
    }
  }
}
.touch-browser .header__menu__sublist {
  bottom: 0;
  top: auto;
  transform: translate(0, 0%);
  left: 5px;
  min-height: auto;
}

