.socials-links {
  position: relative;
  display: inline-flex;
  &__wrapper {
    display: inline-flex;
    flex-wrap: wrap;
    margin: -4px -10px 0;
    position: relative;
  }
  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: (5em/16);
    margin: (4em/16) (5em/16);
    img, svg {
      width: (24em/16);
    }
  }

  &__item + &__item {
    
  }
}

.socials-links--right {
  .socials-links__wrapper {
    justify-content: flex-end;
  }
}

.socials-links--right-sm {
  @include media-breakpoint-up('sm') {
    .socials-links__wrapper {
      justify-content: flex-end;
    }
  }
}

.socials-links--line {
  padding-bottom: (4em/16);
  border-bottom: 1px solid rgba($color-text, 0.5);
}

.socials-links--vertical {
  justify-content: center;
  flex-direction: column;
  .socials-links__wrapper {
    flex-direction: column;
  }
}

.socials-links--vertical.socials-links--line {
  border-bottom: 0;
  padding-bottom: 0;
  &::before, &::after {
    content: '';
    width: 1px;
    min-height: (20em/16);
    height: auto;
    flex: 1 0 auto;
    background: rgba($color-text, 0.5);
    margin: 0 auto;
  }
  &::before {
    margin-bottom: (7em/16);
  }
  &::after {
    margin-top: (7em/16);
  }
}
